import styles from './orderDetails.module.css'
import img from '../../image/done.svg'
import PropTypes from "prop-types";
import { useSelector } from 'react-redux';

const OrderDetails = () => {
    const data = useSelector(state=>state.order.order);
    return (
        <section className={`${styles.section} pt-20 pb-30`} aria-label='  '>
            <p className={`${styles.number} text text_type_digits-large`}>{data.order.number}</p>
            <p className='text text_type_main-medium pt-8'> </p>
            <img className={`${styles.img} pt-15 pb-15`} src={img} alt=" " />
            <p className='text text_type_main-default'>   </p>
            <p className='text text_type_main-default text_color_inactive pt-2'>    </p>
        </section>
    );
};
OrderDetails.propTypes = {
    data: PropTypes.object
}
export default OrderDetails;